@import url(https://fonts.googleapis.com/css?family=Nunito);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --name-color: #3FAA9D;
    --date-color: #6E7173;
    --poi-color: #30D8C3;
    --bg-color-light: #E5E1E1;
    --bg-color-dark: #B8B8B9;
    --card-light: #F4F5F6;
    --icon-color: #A3A1A2;
}

body {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: -webkit-linear-gradient(#E5E1E1, #B8B8B9);
  background-image: linear-gradient(#E5E1E1, #B8B8B9);
  background-image: -webkit-linear-gradient(var(--bg-color-light), var(--bg-color-dark));
  background-image: linear-gradient(var(--bg-color-light), var(--bg-color-dark));
  font-family: 'Nunito', sans-serif;
}
.select {
    font-family: 'Helvetica', sans-serif;
    font-size: 18px;
    margin-top: 50px;
    width: 300px;
    height: 30px;
    border: none;
    color: var(--icon-color);
    padding-left: 10px;
    box-shadow: 2px 2px 10px var(--bg-color-dark);
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
}

.select select {
    border: none;
    box-shadow: none;
    background: transparent;
    background-image: none;
    -webkit-appearance: none;
}

.select:focus {
    outline: none;
}

.option {
    color: black;
}
.card {
    margin-top: 50px;
    width: 350px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 2px 2px 10px var(--bg-color-dark);
}

.card-pic-container {
    border-radius: 10px;
}

.card-pic {
    background-color: var(--card-light);
    width: 350px;
    height: 350px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-position: center;
    background-size: contain;
}

.user-info {
    padding: 20px 25px;
    background-color: var(--card-light);
    width: 100%;
}

.user-username {
    color: var(--icon-color);
}

.user-name {
    color: var(--name-color);
    font-size: 24px;
    font-weight: 600;
}

.user-loca {
    display: block;
    margin-top: 20px;
    font-size: 14px;
    color: var(--date-color);
}

.fa-map-marker-alt {
    color: var(--poi-color)
}

.data-container {
    display: flex;
    justify-content: space-between;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    padding: 20px 40px;
    width: 100%;
    background-image: -webkit-linear-gradient(var(--name-color), var(--poi-color));
    background-image: linear-gradient(var(--name-color), var(--poi-color));
}

.data-box {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.box-number {
    color: white;
    font-size: 40px;
    font-weight: 600;
}

.box-text {
    color: white;
}

.user-date {
    margin-top: 10px;
    color: var(--date-color);
    padding-left: 70px;
}
.App {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.fa-github {
    color: var(--icon-color);
    font-size: 200px;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    z-index: -1;
}

.loading-container {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
